.form-screen {
	position: relative;
	background: $white;
	padding-bottom: 30px;
	&__wrapper {
		width: 100%;
		display: flex; }
	.title {
		margin-left: 196px;
		padding-top: 165px;
		z-index: 1;
		h2, p {
			color: $white; }
		h2 {
			margin-bottom: 9px; }
		p {
			font-family: $default-font;
			font-weight: 300;
			font-size: 21px; } }
	.image {
		&__block {
			position: absolute;
			width: 100%;
			top: 0;
			height: 100%; }
		&__item {
			position: absolute;
			img {
				width: 100%;
				position: relative; }
			&:nth-child(1) {
				left: 50%;
				top: 60px;
				transform: translateX(-960px); }
			&:nth-child(2) {
				left: 50%;
				top: 90px;
				transform: translateX(-456px); }
			&:nth-child(3) {
				left: 50%;
				top: 150px;
				transform: translateX(503px); } } }
	.form__section {
		width: 456px;
		height: 682px;
		background: $san-juan;
		color: $white;
		z-index: 1;
		margin-left: 25px;
		padding: 105px 90px 60px;
		img {
			margin-bottom:  100px; }
		form {
			input {
				width: 100%;
				background: $san-juan;
				padding: 5px 0;
				margin-bottom: 33px;
				&:last-of-type {
					margin-bottom: 42px; } }
			button {
				width: 100%;
				text-align: center;
				background: linear-gradient( 0deg, rgb(254,85,30) 0%, rgb(255,103,53) 46%, rgb(255,121,76) 100%);
				box-shadow: 0 12px 21px 0 rgba(254, 86, 32, 0.2);
				padding: 17px 0;
				text-transform: uppercase;
				font-family: $helper-font;
				font-weight: 700;
				font-size: 12px;
				color: $white; } } } }
