.sixth-screen {
	padding-top: 108px;
	background: $white;
	padding-bottom: 100px;
	.title {
		margin-bottom: 22px;
		h2 {
			margin-bottom: 5px; }
		h3 {
			margin-bottom: 15px; } }

	.swiper-container-two {
		position: relative;
		width: 100%;
		height: 400px;
		.swiper-slide {
			text-align: center;
			font-size: 18px;
			background: #fff;
			/* Center slide text vertically */
			display: flex;
			justify-content: center;
			align-items: center; } }
	.swiper-pagination {
		top: 0;
		transform: translateX(23.9%);
		font-size: 0;
		position: relative; }
	.swiper-pagination-current {
		text-transform: uppercase;
		font-family: $default-font;
		font-size: 31px;
		font-weight: 800;
		color: $mine-shaft;
		&::before {
			content: '';
			padding-right: 5px;
			text-transform: uppercase;
			font-family: $default-font;
			font-size: 31px;
			font-weight: 800;
			color: $mine-shaft; }
		&::after {
			content: '/';
			transform: rotate(22deg) translateX(20px) translateY(-30px);
			position: absolute;
			font-size: 44px;
			color: $azure; } }
	.swiper-pagination-total {
		color: $azure;
		transform: translateY(25px) translateX(15px);
		padding-left: 20px;
		font-size: 21px;
		position: absolute;
		font-family: $default-font;
		font-weight: 800;
		&::before {
			content: '';
			text-transform: uppercase;
			padding-right: 12px; } }
	.swiper-button-next,.swiper-button-prev {
		width: 65px;
		height: 65px;
		background-color: $white;
		box-shadow: 0 12px 21px 0 rgba(0, 0, 0, 0.16);
		top: 69%; }
	.swiper-button-prev {
		left: 19%; }
	.swiper-button-next {
		right: 19%; }
	.swiper-wrapper {
		padding-top: 47px;
		.swiper-slide {
			max-width: 480px;
			position: relative;
			.content {
				text-align: left;
				position: absolute;
				right: 40px;
				top: 12px;
				max-width: 150px;
				h5 {
					font-family: $default-font;
					font-size: 31px;
					font-weight: 800;
					color: $white;
					margin-bottom: 10px;
					text-transform: uppercase; }
				p {
					font-family: $helper-font;
					font-weight: 500;
					font-size: 12px;
					color: $white;
					line-height: 1.5; } } } } }
