.eighth-screen {
	position: relative;
	background: $white;
	padding-top: 100px;
	padding-bottom: 10px;

	.content {
		width: 50%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		h4 {
			font-family: $default-font;
			font-weight: 800;
			font-size: 26px;
			color: $mine-shaft;
			text-transform: uppercase;
			margin-bottom: 10px; }
		p {
			font-family: $helper-font;
			font-weight: 500;
			font-size: 14px;
			color: $tundora;
			line-height: 1.5;
			position: relative;
			&::before {
				content: '';
				width: 70px;
				height: 2px;
				background: $mine-shaft;
				position: absolute;
				bottom: -50px; } }
		.colleciton {
			display: flex;
			flex-wrap: wrap;
			padding-top: 65px;
			.item {
				width: 33%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				p {
					font-weight: 600;
					&::before {
						display: none; } }
				strong {
					font-weight: 400; }
				strong, p {
					font-family: $helper-font;
					font-size: 14px;
					color: $tundora; } } }
		.image {
			position: relative;
			img {
				min-height: 550px;
				padding-left: 15px; } }
		&__wrapper {
			display: flex;
			margin-bottom: 120px;
			&:nth-child(odd) {
				flex-direction: row-reverse;
				.image {
					img {
						float: right;
						padding-right: 15px; } }
				.content {
					padding-left: 15px; } } } } }
