html, body { font-family: "Gilroy",sans-serif; font-weight: 400; font-style: normal; font-size: 16px; overflow-x: hidden; position: relative; min-width: 320px; margin: 0; padding: 0; }

.our { width: 100%; overflow: hidden; height: auto; }

body { background: grey; }

* { margin: 0; padding: 0; }

input, textarea { border-bottom: 2px solid #ffffff; border-radius: 0; outline: none; box-shadow: none; -webkit-appearance: none; -moz-appearance: none; }

input:focus:required:invalid, textarea:focus:required:invalid { border-color: #ffffff; color: #ffffff; outline: none; box-shadow: none; -webkit-appearance: non; }

input:required:valid, textarea:required:valid { border-color: #ffffff; color: #ffffff; outline: none; box-shadow: none; -webkit-appearance: non; }

input::placeholder, textarea::placeholder { color: #ffffff; font-size: 18px; line-height: 2; font-family: "Gilroy", sans-serif; font-weight: 300; }

ul { margin: 0; padding: 0; list-style-type: none; }

a, a:focus, a:hover { outline: none; text-decoration: none; }

.title { margin-bottom: 55px; }

.title h2 { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 43px; line-height: 1; text-transform: uppercase; color: #212121; }

.title h3 { font-family: "Gilroy", sans-serif; font-weight: 300; font-size: 32px; color: #212121; }

.title p { font-family: "Montserrat", sans-serif; font-weight: 400; font-size: 16px; color: #333333; line-height: 1.5; }

.block-title { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 80px; color: #2e62a3; position: absolute; }

.navigation { display: block; position: fixed; width: 100%; font-family: "Roboto", sans-serif; background: #ffffff; z-index: 100; }

.navigation__wrapper { display: flex; flex-direction: row; align-items: center; justify-content: space-between; min-height: 88px; }

.navigation .logo { width: 95px; height: 55px; }

.navigation .nav { width: 610px; }

.navigation .nav ul { display: flex; justify-content: space-between; }

.navigation .nav ul li a { font-size: 14px; color: #2e62a3; }

.navigation .contact { position: relative; }

.navigation .contact a { padding-top: 10px; font-size: 18px; color: #2e62a3; font-weight: 500; text-align: right; display: block; line-height: 1em; padding-left: 45px; }

.navigation .contact a b { font-weight: 700; }

.navigation .contact a strong { font-size: 12px; font-weight: 400; display: block; text-decoration: underline; }

.navigation .contact a img { width: 35px; height: 35px; padding: 10px; position: absolute; border: 1px solid #2e62a3; left: 0; top: 8px; }

.first-screen { padding-top: 250px; padding-bottom: 36px; background: url("../img/first-screen-bg.png") bottom center no-repeat; background-size: cover; }

.first-screen h1 { font-family: "Gilroy", sans-serif; font-weight: 800; color: #ffffff; font-size: 60px; line-height: 1.17; text-transform: uppercase; margin-bottom: 14px; }

.first-screen p { font-family: "Montserrat", sans-serif; font-weight: 400; color: #ffffff; font-size: 21px; line-height: 1.34; margin-bottom: 56px; }

.first-screen .button { font-family: "Montserrat", sans-serif; font-weight: 700; line-height: 2; display: inline-block; margin-bottom: 115px; position: relative; }

.first-screen .button a { display: inline-block; text-transform: uppercase; font-size: 12px; color: #ffffff; background-image: linear-gradient(0deg, #fe551e 0%, #ff6735 46%, #ff794c 100%); box-shadow: 0 12px 21px 0 rgba(254, 86, 32, 0.2); padding: 8px 61px 8px 41px; transition: ease all 0.3s; }

.first-screen .button a::before { content: ''; width: 14px; height: 2px; background: #ffffff; position: absolute; top: 50%; transform: translateY(-50%); right: 15%; }

.first-screen .button a::after { content: ''; width: 8px; height: 8px; border-right: 2px solid #ffffff; border-bottom: 2px solid #ffffff; position: absolute; transform: rotate(-45deg) translateY(-50%); top: 47%; right: 14%; }

.first-screen__info .block { display: flex; flex-direction: column; padding-top: 30px; position: relative; }

.first-screen__info .block p { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 31px; line-height: 0.7; color: #ffffff; margin-bottom: 24px; }

.first-screen__info .block strong { font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 14px; color: #e5e5e5; line-height: 1.5; }

.first-screen__info .block::before { content: ''; width: 170px; height: 1px; background: #ffffff; position: absolute; top: 0; }

.second-screen { padding-top: 75px; background: #ffffff; padding-bottom: 90px; }

.second-screen__block { display: flex; width: 100%; flex-wrap: wrap; }

.second-screen__block .item { width: 50%; }

.second-screen__block .item img { width: 100%; }

.second-screen__block .item p { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 18px; line-height: 1.35; padding-right: 40px; min-height: 60px; display: flex; align-items: center; justify-content: flex-end; }

.second-screen__block .item:nth-child(even) { display: flex; flex-direction: column-reverse; }

.second-screen__block .item:nth-child(even) p { padding-left: 40px; justify-content: start; }

.second-screen__block .item:nth-child(even) img { margin-bottom: 60px; }

.second-screen .button { font-family: "Montserrat", sans-serif; font-weight: 700; line-height: 2; display: flex; justify-content: center; position: relative; margin: 0 auto; }

.second-screen .button a { display: inline-block; text-transform: uppercase; font-size: 12px; color: #fe551e; background: transparent; padding: 8px 61px 8px 41px; transition: ease all 0.3s; border: 1px solid #fe551e; position: relative; }

.second-screen .button a::before { content: ''; width: 1px; height: 11px; background: #fe551e; position: absolute; top: 50%; transform: translateY(-45%); right: 15%; }

.second-screen .button a::after { content: ''; width: 8px; height: 8px; border-right: 1px solid #fe551e; border-bottom: 1px solid #fe551e; position: absolute; transform: rotate(45deg) translateY(-50%) translateX(1px); top: 50%; right: 15%; }

.third-screen { padding-top: 100px; background: #ffffff url("../img/third-screen-bg.png") repeat-x bottom center; padding-bottom: 180px; background-size: cover; position: relative; }

.third-screen .title { margin-bottom: 72px; }

.third-screen .title h2 { margin-bottom: 5px; }

.third-screen .title h3 { margin-bottom: 20px; }

.third-screen .image__block { width: 100%; height: 481px; position: absolute; }

.third-screen .image__item { position: absolute; }

.third-screen .image__item:nth-child(1) { left: -375px; top: 0; }

.third-screen .image__item:nth-child(2) { right: -35px; top: 30px; }

.third-screen__text-block { padding-top: 81px; }

.third-screen__text-block h4, .third-screen__text-block p { margin-left: 65px; }

.third-screen__text-block h4 { font-size: 21px; font-family: "Gilroy", sans-serif; font-weight: 800; text-transform: uppercase; color: #212121; margin-bottom: 13px; }

.third-screen__text-block p { font-family: "Montserrat", sans-serif; font-weight: 400; font-size: 16px; line-height: 1.5; color: #333333; margin-bottom: 41px; }

.third-screen__text-block ul { display: block; max-width: 455px; background: #f5f5f5; margin-left: 35px; padding-top: 25px; padding-bottom: 4px; }

.third-screen__text-block ul li { display: block; max-width: 280px; margin: 0 auto; padding-left: 35px; font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 14px; color: #212121; line-height: 1.49; margin-bottom: 21px; position: relative; }

.third-screen__text-block ul li::before { content: ''; background: url("../img/tick.svg"); width: 14px; height: 14px; position: absolute; left: 0; top: 3px; }

.third-screen .block-title { position: absolute; top: 58%; right: -21%; transform: rotate(90deg) translateY(8px) translateX(4px); }

.third-screen .block-title::before { content: ''; width: 2px; height: 70px; position: absolute; background: #2e62a3; top: 104px; left: 3px; }

.third-screen .container { position: relative; }

.fourth-screen { padding-top: 190px; background: #ffffff; padding-bottom: 220px; position: relative; }

.fourth-screen .title h2 { margin-bottom: 5px; }

.fourth-screen__container { display: flex; flex-wrap: wrap; }

.fourth-screen__block { position: relative; }

.fourth-screen__block:nth-child(1) { width: 54.5%; }

.fourth-screen__block:nth-child(2) { width: 45.5%; }

.fourth-screen__block .block-title { right: -82px; top: 82%; transform: rotate(90deg) translateX(16px); }

.fourth-screen__block .block-title::before { content: ''; width: 2px; height: 70px; position: absolute; background: #2e62a3; top: 23px; left: -27px; }

.fourth-screen .section { width: 100%; padding-top: 105px; display: flex; flex-wrap: wrap; }

.fourth-screen .section__elem { width: 180px; margin-right: 30px; padding-top: 21px; padding-left: 2px; position: relative; }

.fourth-screen .section__elem p { font-family: "Gilroy", sans-serif; font-weight: 800; color: #212121; font-size: 31px; margin-bottom: 8px; }

.fourth-screen .section__elem p::before { content: ''; width: 180px; height: 1px; border-bottom: 1px solid #999999; top: 0; position: absolute; z-index: 1; }

.fourth-screen .section__elem strong { font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 12px; line-height: 1.75; color: #4c4c4c; }

.fourth-screen .section__elem:nth-child(3n+2) { margin-top: 60px; }

.fourth-screen .section__elem:nth-child(3n+3) { margin-top: 120px; }

.fourth-screen .section__elem:nth-child(3n) { margin-right: 0; }

.fourth-screen .section__elem::before { content: '1'; width: 75px; height: 75px; font-family: "Montserrat", sans-serif; color: #f2f2f2; position: absolute; font-size: 120px; font-weight: 700; top: -100px; left: 0; z-index: 0; }

.fourth-screen .section__elem::after { content: ''; width: 180px; height: 30px; background: #ffffff; position: absolute; top: -10px; left: 0; }

.fourth-screen .section__elem:nth-child(1)::before { content: '1'; }

.fourth-screen .section__elem:nth-child(2)::before { content: '2'; }

.fourth-screen .section__elem:nth-child(3)::before { content: '3'; }

.fourth-screen .section__elem:nth-child(4)::before { content: '4'; }

.fourth-screen .section__elem:nth-child(5)::before { content: '5'; }

.fourth-screen .map { position: relative; }

.fourth-screen .map__title { position: absolute; top: -3px; left: 0; line-height: 1.5; background: #ffffff; box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.1); padding: 25px 36px; font-family: "Montserrat", sans-serif; font-weight: 400; font-size: 16px; color: #333333; z-index: 20; }

.fourth-screen .map__image .tabs__wrap { width: 100%; padding-top: 40px; }

.fourth-screen .map__image .tabs__wrap img { min-width: 910px; box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.1); animation: transform 3s ease; }

@keyframes transform { 0% { opacity: 1;
    transform: translateX(0); }
  50% { opacity: 0;
    transform: translateX(100vw); }
  100% { opacity: 1;
    transform: translateX(0); } }

.fourth-screen .map__nav { position: absolute; background: #fafafa; width: 298px; height: 310px; bottom: -135px; z-index: 20; }

.fourth-screen .map__nav ul { display: block; margin-left: 27px; padding-top: 41px; padding-bottom: 41px; }

.fourth-screen .map__nav ul li { font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 14px; margin-bottom: 12px; padding-left: 28px; position: relative; }

.fourth-screen .map__nav ul li a { color: #212121; }

.fourth-screen .map__nav ul li::before { content: ''; width: 7px; height: 7px; border: 1px solid #2e62a3; border-radius: 50%; background: transparent; position: absolute; left: 1px; top: 6px; }

.fourth-screen .map__nav ul li:hover, .fourth-screen .map__nav ul li:focus { cursor: pointer; text-decoration: underline; font-weight: 600; transition: all .3s; }

.fourth-screen .map__nav ul li:hover::before, .fourth-screen .map__nav ul li:focus::before { background: #2e62a3; }

.fourth-screen .map__nav ul li .active { cursor: pointer; text-decoration: underline; font-weight: 600; transition: all .3s; }

.fourth-screen .map__nav ul li .active::before { content: ''; width: 7px; height: 7px; border: 1px solid #2e62a3; border-radius: 50%; position: absolute; left: 1px; top: 6px; background: #2e62a3; }

