.fifth-screen .swiper-container { position: relative; width: 100%; }

.fifth-screen .swiper-container .swiper-slide { display: flex; }

.fifth-screen .swiper-container .swiper-slide .info { padding-top: 84px; padding-right: 40px; width: 50%; text-align: right; }

.fifth-screen .swiper-container .swiper-slide .info h4 { font-size: 21px; font-family: "Gilroy", sans-serif; font-weight: 800; text-transform: uppercase; color: #212121; margin-bottom: 20px; }

.fifth-screen .swiper-container .swiper-slide .info p { font-family: "Montserrat", sans-serif; font-weight: 400; font-size: 16px; line-height: 1.5; color: #333333; margin-bottom: 11px; }

.fifth-screen .swiper-container .swiper-pagination { position: relative; bottom: 0; }

.fifth-screen .ct-chart-wrapper { position: relative; height: 50%; width: 550px; }

.fifth-screen .ct-chart-title { position: absolute; text-align: center; cursor: pointer; color: #212121; }

.fifth-screen .ct-chart-title strong { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 31px; }

.fifth-screen .ct-chart-title p { font-size: 14px; font-family: "Montserrat", sans-serif; font-weight: 500; }

.fifth-screen .ct-chart-title:nth-child(1) { left: 50%; bottom: 10%; }

.fifth-screen .ct-chart-title:nth-child(2) { top: 40%; left: 30%; }

.fifth-screen .ct-chart-title:nth-child(3) { top: 38%; right: 0%; }

.fifth-screen .ct-chart { width: auto; height: 550px; }

.fifth-screen .ct-chart .ct-chart-pie { padding: 20px; stroke: #cccccc; stroke-width: 1; stroke-dasharray: 5 3; cursor: pointer; }

.fifth-screen .ct-chart .my-custom-class-one path { fill: #ffffff; }

.fifth-screen .ct-chart .my-custom-class-one path:hover { fill: #2e62a3; transition: all ease .3s; transform: scale(1.1) translateX(-27px) translateY(-27px); }

.fifth-screen .ct-chart .my-custom-class-two path { fill: #ffffff; }

.fifth-screen .ct-chart .my-custom-class-two path:hover { fill: #2e62a3; transition: all ease .3s; transform: scale(1.1) translateX(-27px) translateY(-27px); }

.fifth-screen .ct-chart .my-custom-class-three path { fill: #ffffff; }

.fifth-screen .ct-chart .my-custom-class-three path:hover { fill: #2e62a3; transition: all ease .3s; transform: scale(1.1) translateX(-27px) translateY(-27px); }

.fifth-screen .ct-chart .active path { fill: #2e62a3; transition: all ease .3s; transform: scale(1.1) translateX(-27px) translateY(-27px); filter: drop-shadow(30px 30px 50px #abc0da); }

.sixth-screen { padding-top: 108px; background: #ffffff; padding-bottom: 100px; }

.sixth-screen .title { margin-bottom: 22px; }

.sixth-screen .title h2 { margin-bottom: 5px; }

.sixth-screen .title h3 { margin-bottom: 15px; }

.sixth-screen .swiper-container-two { position: relative; width: 100%; height: 400px; }

.sixth-screen .swiper-container-two .swiper-slide { text-align: center; font-size: 18px; background: #fff; /* Center slide text vertically */ display: flex; justify-content: center; align-items: center; }

.sixth-screen .swiper-pagination { top: 0; transform: translateX(23.9%); font-size: 0; position: relative; }

.sixth-screen .swiper-pagination-current { text-transform: uppercase; font-family: "Gilroy", sans-serif; font-size: 31px; font-weight: 800; color: #212121; }

.sixth-screen .swiper-pagination-current::before { content: ''; padding-right: 5px; text-transform: uppercase; font-family: "Gilroy", sans-serif; font-size: 31px; font-weight: 800; color: #212121; }

.sixth-screen .swiper-pagination-current::after { content: '/'; transform: rotate(22deg) translateX(20px) translateY(-30px); position: absolute; font-size: 44px; color: #2e62a3; }

.sixth-screen .swiper-pagination-total { color: #2e62a3; transform: translateY(25px) translateX(15px); padding-left: 20px; font-size: 21px; position: absolute; font-family: "Gilroy", sans-serif; font-weight: 800; }

.sixth-screen .swiper-pagination-total::before { content: ''; text-transform: uppercase; padding-right: 12px; }

.sixth-screen .swiper-button-next, .sixth-screen .swiper-button-prev { width: 65px; height: 65px; background-color: #ffffff; box-shadow: 0 12px 21px 0 rgba(0, 0, 0, 0.16); top: 69%; }

.sixth-screen .swiper-button-prev { left: 19%; }

.sixth-screen .swiper-button-next { right: 19%; }

.sixth-screen .swiper-wrapper { padding-top: 47px; }

.sixth-screen .swiper-wrapper .swiper-slide { max-width: 480px; position: relative; }

.sixth-screen .swiper-wrapper .swiper-slide .content { text-align: left; position: absolute; right: 40px; top: 12px; max-width: 150px; }

.sixth-screen .swiper-wrapper .swiper-slide .content h5 { font-family: "Gilroy", sans-serif; font-size: 31px; font-weight: 800; color: #ffffff; margin-bottom: 10px; text-transform: uppercase; }

.sixth-screen .swiper-wrapper .swiper-slide .content p { font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 12px; color: #ffffff; line-height: 1.5; }

.seventh-screen { position: relative; padding-top: 100px; padding-bottom: 50px; background: #fafafa; }

.seventh-screen .title { margin-bottom: 63px; }

.seventh-screen img { width: 57px; height: 57px; margin-bottom: 12px; }

.seventh-screen h5 { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 18px; color: #212121; text-transform: uppercase; margin-bottom: 11px; }

.seventh-screen p { font-family: "Montserrat", sans-serif; font-weight: 500; color: #4c4c4c; line-height: 1.9; font-size: 12px; margin-bottom: 30px; }

.eighth-screen { position: relative; background: #ffffff; padding-top: 100px; padding-bottom: 10px; }

.eighth-screen .content { width: 50%; display: flex; flex-direction: column; justify-content: center; }

.eighth-screen .content h4 { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 26px; color: #212121; text-transform: uppercase; margin-bottom: 10px; }

.eighth-screen .content p { font-family: "Montserrat", sans-serif; font-weight: 500; font-size: 14px; color: #4c4c4c; line-height: 1.5; position: relative; }

.eighth-screen .content p::before { content: ''; width: 70px; height: 2px; background: #212121; position: absolute; bottom: -50px; }

.eighth-screen .content .colleciton { display: flex; flex-wrap: wrap; padding-top: 65px; }

.eighth-screen .content .colleciton .item { width: 33%; display: flex; flex-direction: column; align-items: center; justify-content: center; }

.eighth-screen .content .colleciton .item p { font-weight: 600; }

.eighth-screen .content .colleciton .item p::before { display: none; }

.eighth-screen .content .colleciton .item strong { font-weight: 400; }

.eighth-screen .content .colleciton .item strong, .eighth-screen .content .colleciton .item p { font-family: "Montserrat", sans-serif; font-size: 14px; color: #4c4c4c; }

.eighth-screen .content .image { position: relative; }

.eighth-screen .content .image img { min-height: 550px; padding-left: 15px; }

.eighth-screen .content__wrapper { display: flex; margin-bottom: 120px; }

.eighth-screen .content__wrapper:nth-child(odd) { flex-direction: row-reverse; }

.eighth-screen .content__wrapper:nth-child(odd) .image img { float: right; padding-right: 15px; }

.eighth-screen .content__wrapper:nth-child(odd) .content { padding-left: 15px; }

.ninth-screen { position: relative; padding-top: 120px; padding-bottom: 85px; background: #fafafa; }

.ninth-screen .slide__wrapper { display: flex; flex-direction: row; flex-wrap: wrap; }

.ninth-screen .slide__wrapper .slide { width: 50%; padding-right: 15px; }

.ninth-screen .slide__wrapper .slide:nth-child(even) { padding-left: 15px; padding-right: 0; margin-bottom: 30px; }

.ninth-screen .slide__wrapper .slide__head { font-family: "Montserrat", sans-serif; font-weight: 600; font-size: 14px; color: #212121; background: #f2f2f2; padding-left: 20px; padding-right: 70px; height: 53px; display: flex; align-items: center; position: relative; }

.ninth-screen .slide__wrapper .slide__head::before { content: url("../img/arrow-down.svg"); position: absolute; width: 15px; height: 15px; right: 18px; }

.ninth-screen .slide__wrapper .active::before { content: url("../img/arrow-up.svg"); }

.ninth-screen .slide__wrapper .slide__toggle { font-family: "Montserrat", sans-serif; font-weight: 400; font-size: 14px; color: #4c4c4c; background: #ffffff; border: 1px solid #f2f2f2; padding: 13px 60px 30px 20px; margin-bottom: 30px; display: none; line-height: 1.5; }

.ninth-screen .slide__wrapper .slide__toggle_active { display: block; }

.thent-screen { position: relative; padding-top: 90px; background: #ffffff; padding-bottom: 200px; }

.thent-screen .title { margin-bottom: 58px; }

.thent-screen .title h2 { margin-bottom: 5px; }

.thent-screen .title h3 { margin-bottom: 36px; }

.thent-screen .title p { width: 570px; }

.thent-screen .title p a { color: #333333; font-family: "Montserrat", sans-serif; font-weight: 500; text-decoration: underline; }

.thent-screen__content { display: flex; flex-direction: row; }

.thent-screen__content .block { width: 50%; }

.thent-screen__content .block__info { display: flex; flex-wrap: wrap; padding-top: 28px; }

.thent-screen__content .block__elem { width: 50%; padding-right: 30px; }

.thent-screen__content .block__elem img { width: 57px; margin-bottom: 12px; }

.thent-screen__content .block__elem h5 { font-family: "Gilroy", sans-serif; font-weight: 800; font-size: 18px; color: #212121; text-transform: uppercase; margin-bottom: 11px; }

.thent-screen__content .block__elem p { font-family: "Montserrat", sans-serif; font-weight: 500; color: #4c4c4c; line-height: 1.9; font-size: 12px; margin-bottom: 66px; }

.thent-screen__content .block__elem:nth-child(even) { padding-left: 12px; padding-right: 0; }

.thent-screen__content .block__image { position: relative; padding-left: 17px; }

.thent-screen__content .block__image img:nth-child(even) { position: absolute; bottom: -110px; left: 35px; }

.form-screen { position: relative; background: #ffffff; padding-bottom: 30px; }

.form-screen__wrapper { width: 100%; display: flex; }

.form-screen .title { margin-left: 196px; padding-top: 165px; z-index: 1; }

.form-screen .title h2, .form-screen .title p { color: #ffffff; }

.form-screen .title h2 { margin-bottom: 9px; }

.form-screen .title p { font-family: "Gilroy", sans-serif; font-weight: 300; font-size: 21px; }

.form-screen .image__block { position: absolute; width: 100%; top: 0; height: 100%; }

.form-screen .image__item { position: absolute; }

.form-screen .image__item img { width: 100%; position: relative; }

.form-screen .image__item:nth-child(1) { left: 50%; top: 60px; transform: translateX(-960px); }

.form-screen .image__item:nth-child(2) { left: 50%; top: 90px; transform: translateX(-456px); }

.form-screen .image__item:nth-child(3) { left: 50%; top: 150px; transform: translateX(503px); }

.form-screen .form__section { width: 456px; height: 682px; background: #2e4868; color: #ffffff; z-index: 1; margin-left: 25px; padding: 105px 90px 60px; }

.form-screen .form__section img { margin-bottom: 100px; }

.form-screen .form__section form input { width: 100%; background: #2e4868; padding: 5px 0; margin-bottom: 33px; }

.form-screen .form__section form input:last-of-type { margin-bottom: 42px; }

.form-screen .form__section form button { width: 100%; text-align: center; background: linear-gradient(0deg, #fe551e 0%, #ff6735 46%, #ff794c 100%); box-shadow: 0 12px 21px 0 rgba(254, 86, 32, 0.2); padding: 17px 0; text-transform: uppercase; font-family: "Montserrat", sans-serif; font-weight: 700; font-size: 12px; color: #ffffff; }
