.thent-screen {
	position: relative;
	padding-top: 90px;
	background: $white;
	padding-bottom: 200px;
	.title {
		margin-bottom: 58px;
		h2 {
			margin-bottom: 5px; }
		h3 {
			margin-bottom: 36px; }
		p {
			width: 570px;
			a {
				color: $mine-shaft-lighter;
				font-family: $helper-font;
				font-weight: 500;
				text-decoration: underline; } } }

	&__content {
		display: flex;
		flex-direction: row;
		.block {
			width: 50%;
			&__info {
				display: flex;
				flex-wrap: wrap;
				padding-top: 28px; }
			&__elem {
				width: 50%;
				padding-right: 30px;
				img {
					width: 57px;
					margin-bottom: 12px; }
				h5 {
					font-family: $default-font;
					font-weight: 800;
					font-size: 18px;
					color: $mine-shaft;
					text-transform: uppercase;
					margin-bottom: 11px; }
				p {
					font-family: $helper-font;
					font-weight: 500;
					color: $tundora;
					line-height: 1.9;
					font-size: 12px;
					margin-bottom:  66px; }
				&:nth-child(even) {
					padding-left: 12px;
					padding-right: 0; } }
			&__image {
				position: relative;
				padding-left: 17px;
				img {
					&:nth-child(even) {
						position: absolute;
						bottom: -110px;
						left: 35px; } } } } } }
