.fifth-screen {
	padding-top: 175px;
	background: url("../img/fifth-screen-bg.png") top center no-repeat $white;
	padding-bottom: 140px;
	background-size: cover;
	.swiper-container {
		position: relative;
		width: 100%;
		.swiper-slide {
			display: flex;
			.info {
				padding-top: 84px;
				padding-right: 40px;
				width: 50%;
				text-align: right;
				h4 {
					font-size: 21px;
					font-family: $default-font;
					font-weight: 800;
					text-transform: uppercase;
					color: $mine-shaft;
					margin-bottom: 20px; }
				p {
					font-family: $helper-font;
					font-weight: 400;
					font-size: 16px;
					line-height: 1.5;
					color: $mine-shaft-lighter;
					margin-bottom: 11px; } } }
		.swiper-pagination {
			position: relative;
			bottom: 0; } }
	.ct-chart-wrapper {
		position: relative;
		height: 50%;
		width: 550px; }
	.ct-chart-title {
		position: absolute;
		text-align: center;
		cursor: pointer;
		color: $mine-shaft;

		strong {
			font-family: $default-font;
			font-weight: 800;
			font-size: 31px; }
		p {
			font-size: 14px;
			font-family: $helper-font;
			font-weight: 500; }
		&:nth-child(1) {
			left: 50%;
			bottom: 10%; }
		&:nth-child(2) {
			top: 40%;
			left: 30%; }
		&:nth-child(3) {
			top: 38%;
			right: 0%; } }

	.ct-chart {
			width: auto;
			height: 550px;
			.ct-chart-pie {
				padding: 20px;
				stroke: $silver;
				stroke-width: 1;
				stroke-dasharray: 5 3;
				cursor: pointer; }
			.my-custom-class-one {
				path {
					fill: #ffffff;
					&:hover {
						fill: $azure;
						transition: all ease .3s;
						transform: scale(1.1) translateX(-27px) translateY(-27px); } } }
			.my-custom-class-two {
				path {
					fill: #ffffff;
					&:hover {
						fill: $azure;
						transition: all ease .3s;
						transform: scale(1.1) translateX(-27px) translateY(-27px); } } }
			.my-custom-class-three {
				path {
					fill: #ffffff;
					&:hover {
						fill: $azure;
						transition: all ease .3s;
						transform: scale(1.1) translateX(-27px) translateY(-27px); } } }
			.active {
				path {
					fill: $azure;
					transition: all ease .3s;
					transform: scale(1.1) translateX(-27px) translateY(-27px);
					filter: drop-shadow(30px 30px 50px #abc0da); } } } }
