.navigation {
	display: block;
	position: fixed;
	width: 100%;
	font-family: $nav-font;
	background: #ffffff;
	z-index: 100;
	&__wrapper {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		min-height: 88px; }
	.logo {
		width: 95px;
		height: 55px; }
	.nav {
		width: 610px;
		ul {
			display: flex;
			justify-content: space-between;
			li {
				a {
					font-size: 14px;
					color: $azure; } } } }
	.contact {
		position: relative;
		a {
			padding-top: 10px;
			font-size: 18px;
			color: $azure;
			font-weight: 500;
			text-align: right;
			display: block;
			line-height: 1em;
			padding-left: 45px;
			b {
				font-weight: 700; }
			strong {
				font-size: 12px;
				font-weight: 400;
				display: block;
				text-decoration: underline; }
			img {
				width: 35px;
				height: 35px;
				padding: 10px;
				position: absolute;
				border: 1px solid $azure;
				left: 0;
				top: 8px; } } } }
