.second-screen {
	padding-top: 75px;
	background: $white;
	padding-bottom: 90px;
	&__block {
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		.item {
			width: 50%;
			img {
				width: 100%; }
			p {
				font-family: $default-font;
				font-weight: 800;
				font-size: 18px;
				line-height: 1.35;
				padding-right: 40px;
				min-height: 60px;
				display: flex;
				align-items: center;
				justify-content: flex-end; }
			&:nth-child(even) {
				display: flex;
				flex-direction: column-reverse;
				p {
					padding-left: 40px;
					justify-content: start; }
				img {
					margin-bottom: 60px; } } } }
	.button {
		font-family: $helper-font;
		font-weight: 700;
		line-height: 2;
		display: flex;
		justify-content: center;
		position: relative;
		margin: 0 auto;
		a {
			display: inline-block;
			text-transform: uppercase;
			font-size: 12px;
			color: $orange;
			background: transparent;
			padding: 8px 61px 8px 41px;
			transition: ease all 0.3s;
			border: 1px solid $orange;
			position: relative;
			&::before {
				content: '';
				width: 1px;
				height: 11px;
				background: $orange;
				position: absolute;
				top: 50%;
				transform: translateY(-45%);
				right: 15%; }
			&::after {
				content: '';
				width: 8px;
				height: 8px;
				border-right: 1px solid $orange;
				border-bottom: 1px solid $orange;
				position: absolute;
				transform: rotate(45deg) translateY(-50%) translateX(1px);
				top: 50%;
				right: 15%; } } } }
